*,
::before,
::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

body {
  color: #21242E;
  background-color: #ffffff;
  font-family: "Roboto", sans-serif;
  letter-spacing: 0.03em;
  font-size: 14px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
}

button {
  border: none;
  background-color: transparent;
  cursor: url("/dist/images/cursor.svg"), auto;
}

.container {
  width: 100% x;
  min-width: 320px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}
a {
  text-decoration: none;
  cursor: url("/dist/images/cursor.svg"), auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

ol, ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.header__link {
  color: #D0BC7A;
}

.header__link svg {
  display: block;
  width: 32px;
  height: 32px;
  fill: currentColor;
  stroke: currentColor;
}